.app-wrapper {
      display: grid;
      grid-template-areas: 
      "h h"
      "n c";
      grid-template-rows: 150px 1fr;
      grid-template-columns: 3fr 9fr;
      /*grid-gap: 10px;*/
      margin: 0 auto;
      width: 1200px;
}
.app-wrapper-content {
  grid-area: c;
  background-color: cornflowerblue;
}





